import { objectFunctions } from "../../../engine/objectHandlers/objectFunctions";
import { normalBullet } from "../../../objects/bullets/normalBullet";
import { IWeapon } from "../IWeapon";


export class nw1 implements IWeapon{
    private active = false;
    private timer = 10;
    private timerNumber = 10;

    activate(){
        if(this.active == false){
            this.active = true;
        }
    }

    deactivate(): void {
        if(this.active){
            this.active = false;
        }
    }

    poll(l: objectFunctions, x: number, y: number, angle: number): void{
        if(this.timer == 0){
            let bullet = new normalBullet(x, y, "");
            l.addObjectLayerName(bullet, "main");
            this.timer = this.timerNumber;
        }else{
            this.timer--;
        }
    }
}