import { objectBase } from "../engine/objectHandlers/objectBase";
import { objectFunctions } from "../engine/objectHandlers/objectFunctions";
import { io } from "socket.io-client";
import { human } from "./human";


export class socketHandler extends objectBase
{
    static objectName = "socketHandler";

    socket;
    gameId = "";
    socketId = "";

    localPlayerId = "";
    constructor(xp: number, yp: number, input: string) {
        super(xp, yp, socketHandler.objectName);

        this.socket = io("ws://localhost:3000");


        
    }

    init(roomEvents: objectFunctions): void {
        this.socket.on("connect", () => {
            this.socket.emit("startGame");
        });


        this.socket.on("gameRoomFound", data => {
            this.gameId = data.gameId;
            this.socketId = data.mySocketId;

            let playerNew = new human(this.g.x, this.g.y, "true");
            roomEvents.addObjectLayerName(playerNew, "main");
            this.localPlayerId = playerNew.ID;

            this.socket.on("playerPositions", playerPositions => {
                console.log("playerPositions: ",playerPositions);
                let objectIds = Object.keys(playerPositions);
                objectIds.forEach(plPosition => {
                    let positionData = playerPositions[plPosition];
                    /**gravity: [pl.gravity.Dx, pl.gravity.Dy],
                    force: [pl.force.Dx, pl.force.Dy, pl.force.delta, pl.force.magnitude] */
                    if(positionData.id != this.localPlayerId){
                        let allHumans = roomEvents.getSpecificObjects(human.objectName) as human[];
                        let humanUpdated = false;

                        allHumans.forEach(h => {
                            if(h.ID == positionData.id){

                                h.force.Dx = positionData.force[0];
                                h.force.Dy = positionData.force[1];
                                h.setPersistentForce(positionData.force[2], positionData.force[3]);
                                //h.addForceAngleMagnitude(positionData.force[0], (1/13)*positionData.force[1]);

                                h.gravity.Dx = positionData.gravity[0];
                                h.gravity.Dy = positionData.gravity[1];

                                h.g.x = positionData.x;
                                h.g.y = positionData.y;

                                humanUpdated = true;
                            }
                        });
                        
                        if(humanUpdated == false){
                            let playerNew = new human(positionData.x, positionData.y, "false");
                            playerNew.ID = positionData.id;
                            roomEvents.addObjectLayerName(playerNew, "main");
                        }
                    }
                    
                    
                    
                    
                    
                });
            });
        });
    }
    logic(l: objectFunctions){
        super.logic(l);
    }


    public updatePlayerPosition(pl: human){
        let sendPlData = {
            id: pl.ID,
            x: pl.g.x,
            y: pl.g.y,
            gravity: [pl.gravity.Dx, pl.gravity.Dy],
            force: [pl.force.Dx, pl.force.Dy, pl.force.delta, pl.force.magnitude],
            gameId: this.gameId,
            socketId: this.socketId,
        };
        //console.log("Send pl data: ",sendPlData);
        this.socket.emit("position", sendPlData);
    }

}