import { objectBase } from "../engine/objectHandlers/objectBase";
import * as PIXI from 'pixi.js'
import { objectFunctions } from "../engine/objectHandlers/objectFunctions";
import { baseAttackNull } from "../engine/hitboxes/baseAttack/baseAttackNull";
import { characterMoves } from "../code/characterMoves";
import { iVector } from "../engine/dataObjects/vector/iVector";
import { vectorFixedDelta } from "../engine/dataObjects/vector/vectorFixedDelta";
import { calculations } from "../engine/calculations";
import { socketHandler } from "./socketHandler";
import { iObject } from "../engine/objectHandlers/iObject";
import { nulliObject } from "../engine/objectHandlers/nulliObject";
import { block1 } from "./block1";
import { weaponEmpty } from "../code/weapons/weaponEmpty";
import { IWeapon } from "../code/weapons/IWeapon";
import { nw1 } from "../code/weapons/normalWeapons/nw1";

export class human extends objectBase
{
    static objectName = "human";
    sameLayerCollisionOnly = true;

    collidesWithPolygonGeometry = true;

    private characterMoveBase;
    
    airFriction: number = 0.80;
    friction = 0.7;
    gravity: iVector = new vectorFixedDelta(calculations.degreesToRadians(270), 0);//vector.fromAngleAndMagnitude(calculations.degreesToRadians(270), 0.6);
    weight:number =  0.0;

    socketHandler: socketHandler | null = null;
    isCurrentPlayer = false;

    online_persistentForceDelta = 0;
    online_persistentForceMagnitude = 0;

    currentWeapon: IWeapon = new weaponEmpty();

    constructor(xp: number, yp: number, input: string) {
        super(xp, yp, human.objectName);
        super.setCollision(0, 0, 58, 58);

        if(input == "true"){
            this.isCurrentPlayer = true;
        }
        super.style((g: PIXI.Container) => {
            let newGraphics = new PIXI.Graphics();

            newGraphics.beginFill(0xFF3e50);
            newGraphics.drawRect(0, 0, 58, 58);
            newGraphics.endFill();
            g.addChild(newGraphics);

            g.calculateBounds();

            return g;
        });



        this.characterMoveBase = new characterMoves("", (l: objectFunctions) => {
            //Ground attacks
            return new baseAttackNull();
        }, (l: objectFunctions) => {
            //air attacks

            return new baseAttackNull();
        });


        this.addCollisionTarget(block1.objectName);
        this.currentWeapon = new nw1();
    }


    afterInit(roomEvents: objectFunctions): void {
        this.socketHandler = roomEvents.getSpecificObjects(socketHandler.objectName)[0] as socketHandler;
    }

    logic(l: objectFunctions){
        super.logic(l);
        if(this.isCurrentPlayer && this.socketHandler != null){
            if(l.flags().has("freezeObjects")){
                this.characterMoveBase.move(l, this);
            }
            
            if(l.checkMouseClick()){
                this.currentWeapon.activate();
            }else{
                this.currentWeapon.deactivate();
            }

            this.currentWeapon.poll(l, this.g.x, this.g.y, 0);
            
            l.setCameraTarget(this.g.x + this.collisionBox.x + (this.collisionBox.width/2), this.g.y);
            

            this.socketHandler.updatePlayerPosition(this);
        }else{
            //Is online player
            //persistent force
            this.addForceAngleMagnitude(this.online_persistentForceDelta, (1/13)*this.online_persistentForceMagnitude);
            this.online_persistentForceMagnitude *= 0.9;
        }

    }

    setPersistentForce(delta: number, magnitude: number){
        this.online_persistentForceDelta = delta;
        this.online_persistentForceMagnitude = magnitude;
    }

}